  },
  methods: {
    // 选择牌面背景
    selectCardBg (url) {
      this.curCardBg = url
      this.cardList.forEach((item) => {
        item.bImg = this.curCardBg
      })
    },
    selectDesktopBg (url) {
      this.curDesktopBg = url
    },
    closeTime () {
      clearInterval(this.timer)
      this.timer = null
    },
    updateTime () {
      this.timer = setInterval(() => {
        let hours = new Date().getHours()
        let minutes = new Date().getMinutes()
        let curHours = hours < 10 ? ('0' + hours) : hours
        let curMinutes = minutes < 10 ? ('0' + minutes) : minutes
        this.date = curHours + '：' + curMinutes
      }, 1000)
    },
    registerFn () {
      let vm = this
      // 获取系统电量
      this.$JsBridge.registerHandler('djUpdatePower', function (data, responseCallback) {
        let powerSize = data
        if (powerSize <= 100 && powerSize > 75) {
          vm.power = ['green', 'green', 'green', 'green']
        } else if (powerSize <= 75 && powerSize > 50) {
          vm.power = ['green', 'green', 'green', '']
        } else if (powerSize <= 50 && powerSize > 25) {
          vm.power = ['green', 'green', '', '']
        } else {
          vm.power = ['red', '', '', '']
        }
      })
      // 系统更新加入游戏的用户列表
      this.$JsBridge.registerHandler('djUpdateUsers', function (data, responseCallback) {
        // 将原生带来的参数，显示在show标签位置
        let arrs = []
        arrs = window.JSON.parse(data)
        vm.updateUsers(arrs)
        // 调用responseCallback方法可以带传参数到原生
        responseCallback('')
      })
      // 系统发牌
      this.$JsBridge.registerHandler('djUpdateCards', function (data, responseCallback) {
        // 将原生带来的参数，显示在show标签位置
        let list = window.JSON.parse(data)
        vm.updateCards(list)
        // 调用responseCallback方法可以带传参数到原生
        responseCallback('')
      })
      // // 系统更新投注结果
      this.$JsBridge.registerHandler('djUpdateCoins', function (data, responseCallback) {
        // 将原生带来的参数，显示在show标签位置
        let coins = window.JSON.parse(data)
        vm.updateCoins(coins)
        // vm.showDj = false
        // vm.$emit('on-close', vm.showDj)
        // 调用responseCallback方法可以带传参数到原生
        responseCallback('')
      })
      // 投注后更新结果
      this.$JsBridge.registerHandler('djUpdateResult', function (data, responseCallback) {
        // 将原生带来的参数，显示在show标签位置
     //   vm.coinList = []
        let res = window.JSON.parse(data)
        let result = res.userGameVOList // 结果
        let list = res.doorVOList // 第二张牌
        vm.updateResult(result, list, res.gameover)
        // 调用responseCallback方法可以带传参数到原生
        responseCallback('')
      })
       // 游戏未开始解散房间
      this.$JsBridge.registerHandler('djReleaseWait', function (data, responseCallback) {
        console.log('游戏未开始解散房间')
        // 将原生带来的参数，显示在show标签位置
       // vm.users = window.JSON.parse(data)
        let msg = '房主解散了房间'
        vm.closeTime()
        vm.resetParams()
        vm.nextResetParams()
        vm.lowz.pop()
        vm.showReleaseWaitModal = false
        vm.isFirst = true
        vm.isGameStart = false
        vm.curRound = 1
        vm.users = []
        vm.$emit('on-close', msg)
        // 调用responseCallback方法可以带传参数到原生
        responseCallback('')
      })
      // 游戏中解散房间
      this.$JsBridge.registerHandler('djReleaseReady', function (data, responseCallback) {
        // 将原生带来的参数，显示在show标签位置
        console.log('游戏中解散房间')
        let flag = false
        let user = window.JSON.parse(data)
        console.log(data)
        console.log(user.disband)
        if (user.disband === true) {
         // let msg = '房间已经解散'
          vm.closeTime()
         // vm.resetParams()
          vm.gameOver()
          vm.lowz.pop()
          vm.showReleaseReadyModal = false
          vm.isFirst = true
          vm.isGameStart = false
          vm.curRound = 1
          vm.users = []
          vm.showXzModal = false
         // vm.$emit('on-close', msg)
        } else {
          let reject = false
          user.dissolveUserList.forEach((its) => {
            if (its.agree === false) {
              reject = true
            }
          })
          if (!reject) {
            vm.releaseReadyText = '玩家【' + user.opeUserId + '】申请解散房间，请等待其他玩家选择(超过120秒未作选择则默认该玩家同意)玩家【' + vm.userId + '】等待选择'
            vm.showReleaseReadyModal = true
            vm.showTimer = true
            vm.sTimer1 = setInterval(() => {
              if (vm.sortTimer1 <= 0) {
                clearInterval(vm.sTimer1)
                vm.sTimer1 = null
                vm.sortTimer1 = 120
                vm.showTimer = false
              }
              vm.sortTimer1 -= 1
            }, 1000)
            if (Number(user.opeUserId) !== Number(vm.userId)) {
              user.dissolveUserList.forEach((its) => {
                if (Number(its.userId) === Number(vm.userId)) {
                  flag = true
                }
              })
              if (!flag && !reject) {
                vm.showReleaseRoomBtns = true
                vm.disbandType = 2
                vm.sTimer2 = setInterval(() => {
                  if (vm.sortTimer2 <= 0) {
                    console.log('超时了....')
                    vm.releaseReadyOk()
                  }
                  vm.sortTimer2 -= 1
                }, 1000)
              } else {
              //  vm.showReleaseReadyModal = false
              }
            } else {
              vm.showReleaseRoomBtns = false
            // vm.showReleaseReadyModal = false
            }
          } else {
            vm.showReleaseRoomBtns = false
            vm.showReleaseReadyModal = false
            vm.sTimer1 = null
            vm.sortTimer1 = 120
            vm.showTimer = false
          }
        }
        // 调用responseCallback方法可以带传参数到原生
        responseCallback('')
      })
       // 断线重新连接
      this.$JsBridge.registerHandler('djResetConnect', function (data, responseCallback) {
        let sign = vm.$sn({
          userId: vm.userId,
          roomId: vm.roomId
        })
        let params = window.JSON.stringify({
          host: this.$url,
          path: this.$interface['/app'],
          params: {
            command: 1013,
            data: {
              userId: vm.userId,
              roomId: vm.roomId,
              sign: sign
            }
          }
        })
        responseCallback(params)
      })
      this.$JsBridge.registerHandler('djConnectGame', function (data, responseCallback) {
        console.log(data)
        console.log('断线重连了')
        let res = window.JSON.parse(data)
        if (res.needConnection === true) {
          if (res.turnStep === 'UN_START' || res.turnStep === 'UN_BLOCK') {
            vm.updateUsers(res.roomUserVOList)
          } else if (res.turnStep === 'HAS_BLOCK') {
            console.log('哈哈哈')
            vm.isMaster = false
            vm.isStartReady = false
            vm.updateUsers(res.roomUserVOList)
            vm.updateCards(res.doorVOList)
          } else if (res.turnStep === 'BETTING') {
            vm.updateUsers(res.roomUserVOList)
            vm.updateCards(res.doorVOList)
            vm.updateResult(res.userGameVOList)
          } else if (res.turnStep === 'UN_PUSH_CARD' || res.turnStep === 'HAS_PUSH_CARD') {
           // vm.updateUsers(res.roomUserVOList)
           // vm.updateCards(res.doorVOList)
          }
        }
        responseCallback('')
      })
    },
    // 判断两个数组所包含内容是否相同
    regCard (arr1, arr2) {
      if (arr1.length !== arr2.length) {
        return false
      }
      for (let i = 0; i < arr1.length; i++) {
        if (arr1[i].toString() !== arr2[i].toString()) {
          return false
        }
      }
      return true
    },
    getRoomMsg () {
      if (this.lowz.length >= 4) {
        return
      }
      if (Number(this.gameMsg.baseScore) === 20) {
        this.lowz.push(this.lowzMax[0])
      } else if (this.gameMsg.baseScore === 50) {
        this.lowz.push(this.lowzMax[1])
      } else if (this.gameMsg.baseScore === 100) {
        this.lowz.push(this.lowzMax[2])
      } else if (this.gameMsg.baseScore === 200) {
        this.lowz.push(this.lowzMax[3])
      }
    },
    isAllUserReady (users) {
      for (let i = 0, len = users.length; i < len; i++) {
        if (users[i].ready === false) {
          // 有用户未准备就绪
          return false
        }
      }
      return true
    },
    isOtherReady (users) {
      let datas = []
      users.forEach((item) => {
        if (String(item.userId) !== String(this.userId)) {
          datas.push(item)
        }
      })
      for (let i = 0, len = datas.length; i < len; i++) {
        if (datas[i].ready === false) {
          // 有用户未准备就绪
          return false
        }
      }
      return true
    },
