        margin-left: 30px;
        vertical-align: top;
      }
      .power{
        display: inline-block;
        vertical-align: middle;
        li{
          display: inline-block;
          position: relative;
          width: 1vh;
          height: 8vh;
          line-height: 8vh;
          margin-right: 5px;
          span{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
