.xz-modal{
  .xz-body{
    ul{
      margin: 12px 12px 0 12px;
      li.item{
        display: flex;
        flex-direction: row;
        margin-bottom: 24px;
        .label{
          // flex: 0 0 150px;
          // width: 150px;
          flex: 0 0 100px;
          width: 100px;
          display: flex;
          align-items: center;
          img{
            width: 100%;
            vertical-align: middle;
          }
        }
        .select-xz{
          // flex: 1;
          // width: 890px;
          flex: 0 0 auto;
          width: auto;
          height: 78px;
          margin-left: 12px;
          background: #6dc4d8;
          border-radius: 30px;
          .nums{
            display: inline-block;
            .num{
              display: inline-block;
              width: 78px;
              height: 78px;
              margin-left: 20px;
              img{
                display: inline-block;
                width: 100%;
                vertical-align: middle;
                margin-top: 6px;
              }
            }
            .num:first-child{
              margin-left: 0;
            }
          }
          .total{
            position: relative;
            display: inline-block;
            width: 160px;
            height: 60px;
            line-height: 60px;
            text-align: left;
            font-family: 'microsoft yahei';
            font-size: 56px;
            font-weight: bold;
            padding-left: 10px;
            margin: 10px 10px 0;
            color: #000;
            background: #fff;
            border-radius: 10px;
            vertical-align: middle;
            .close{
              position: absolute;
              top: 0;
              right: 6px;
              width: 80px;
              height: 100%;
              img{
                width: 100%;
              }
            }
          }
        }
      }
      li.item:last-child{
        margin-bottom: 0;
      }
    }
  }
  .xz-foot{
     padding: 0 10%;
     text-align: center;
    .xz-btn{
      position: relative;
      top: 15px;
      display: inline-block;
      width: 38%;
      height: 60px;
    }
  }
}
