.share-modal{
  .share-body{
    padding: 72px 0 10px 0;
    color: #fff;
    font-size: 48px;
    overflow-x: auto;
    ul{
      display: flex;
    }
    li{
      flex: 1;
      margin: 0 10px;
      .user-tit{
      //  display: flex;
        padding: 10px 12px 0;
        text-align: left;
        .user-avatar{
          position: relative;
          flex: 0 0 100px;
          width: 100px;
          height: 100px;
          line-height: 100px;
          border-radius: 16px;
          overflow: hidden;
          background-image: url('../assets/imgs/Avatarframe.png');
          background-size: 100% 100%;
          .user-img{
            position: absolute;
             top: -5px;
            left: -4px;
            width: 96px;
            height: 96px;
            padding: 15px;
            border-radius: 20px;
            overflow: hidden;
            img{
              width: 78px;
              height: 78px;
              border-radius: 16px;
              border: 1px solid #000;
              box-shadow: 0px 0px 10px #000;
            }
          }
        }
        .user-id-card{
          display: inline-block;
          flex: 1;
        //  width: calc(~"100% - 200px");
       //   padding-left: 15px;
        //  margin-right: 30px;
          font-size: 32px;
          text-align: left;
          .user-name{
          //  padding: 0 15px 0 0;
            margin: 5px 0;
            border-radius: 12px;
          //  background: rgba(0, 0, 0, .5) url('../assets/imgs/img-End-netname.png') 0 0 no-repeat;
          //  background-size: contain;
            img{
              height: 42px;
              vertical-align: middle;
            }
            span{
              display: inline-block;
              vertical-align: middle;
             // width: 62%;
              width: 68%;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
          }
          .user-id{
          //  padding: 0 15px 0 0;
            margin: 5px 0;
            border-radius: 12px;
          //  background: rgba(0, 0, 0, .5) url('../assets/imgs/img-End-id.png') 0 0 no-repeat;
           // background-size: contain;
            img{
              height: 42px;
              vertical-align: middle;
            }
            span{
              display: inline-block;
              vertical-align: middle;
            }
          }
        }
      }
      .item{
        position: relative;
        text-align: left;
        margin: 0 20px;
        height: 56px;
        display: flex;
        img{
          flex: 0 0 156px;
          width: 156px;
          height: 32px;
          margin-top: 10px;
        }
        .num{
          font-size: 32px;
         // position: absolute;
         position: relative;
          top: -6px;
         // right: 20px;
          flex: 1;
           margin-top: 10px;
          text-align: right;
          vertical-align: bottom;
        }
        .line{
          position: absolute;
          bottom: 7px;
          left: 0;
          width: 100%;
          height: 2px;
          background: #efefef;
        }
      }
      .item:last-child{
        img{
           flex: 0 0 124px;
            width: 124px;
            height: 24px;
        }
      }
    }
    li.winBg{
      border-radius: 24px;
      background-image: url('../assets/imgs/img-End-smallbackground1.png');
      background-size: 100% 130%;
    }
    li.loseBg{
      border-radius: 24px;
      background-image: url('../assets/imgs/img-End-smallbackground2.png');
      background-size: 100% 130%;
    }
  }
  .share-foot{
