          item.img = tabImgs.imgs[ids]
        }
        if (index === 0) {
          if (this.isFirstQt) {
            this.loadQt = true
            this.isFirstQt = false
          }
          this.qtAjax()
        } else if (index === 1) {
          if (this.isFirstHt) {
            this.loadHt = true
            this.isFirstHt = false
          }
          this.htAjax()
        } else if (index === 2) {
          if (this.isFirstDj) {
            this.loadDj = true
            this.isFirstDj = false
          }
          this.djAjax()
        }
      })
      this.selectGame = index
    },
    // 关闭规则弹窗
    closeRuleModal () {
      this.showRuleModal = false
    },
    // 设置弹窗
    setting (e) {
      this.$audio.play(this.$audio.ui)
      this.$refs.setting.openSetModal()
    },
    // 创建房间弹窗
    createRoom (e) {
      this.$audio.play(this.$audio.btn)
      this.showCreateRoom = true
      this.createRoomData.substitute = false
    },
    // 创建房间类型切换（清推、混推、大九）
    changeRoom (index, e) {
      this.$audio.play(this.$audio.ui)
      this.createRoomTabs.forEach((item, ids) => {
        if (ids === index) {
          item.img = tabImgs.hoverImgs[ids]
        } else {
          item.img = tabImgs.imgs[ids]
        }
      })
      // 保存选择的游戏类型
      this.selectTypes = index
      this.selectRoom = index
    },
    // 清推局数选择
    selectTime1 (data) {
      console.log(data)
      this.$audio.play(this.$audio.ui)
      this.createRoomData1.round = data
    },
    // 清推分数选择
    selectScore1 (data) {
      this.$audio.play(this.$audio.ui)
      this.createRoomData1.score = data
    },
    // 混推局数选择
    selectTime2 (data) {
      this.$audio.play(this.$audio.ui)
      this.createRoomData2.round = data
    },
    // 混推分数选择
    selectScore2 (data) {
      this.$audio.play(this.$audio.ui)
      this.createRoomData2.score = data
    },
    // 大九局数选择
    selectTime3 (data) {
      this.$audio.play(this.$audio.ui)
      this.createRoomData3.round = data
    },
    // 大九分数选择
    selectScore3 (data) {
      this.$audio.play(this.$audio.ui)
      this.createRoomData3.score = data
    },
    // 创建房间
    createGameRoom (e) {
      this.$audio.play(this.$audio.btn)
      if (this.selectTypes === 0) {
        this.createRoomData1.substitute = false
      } else if (this.selectTypes === 1) {
        this.createRoomData2.substitute = false
      } else {
        this.createRoomData3.substitute = false
      }
      this.selectData = {}
      this.create()
    },
    // 是否代开房间
    invoiceGameRoom (e) {
      this.$audio.play(this.$audio.btn)
      this.selectData = {}
      if (this.selectTypes === 0) {
        this.createRoomData1.substitute = true
        this.dkRound = this.ds1_1
        this.dkScore = this.ds1_2
        // if (this.isFirstQt) {
        //   this.qtAjax()
        //   this.isFirstQt = false
        // } else {
        //   this.dkRules = this.qtRules
        // }
      } else if (this.selectTypes === 1) {
        this.createRoomData2.substitute = true
        this.dkRound = this.ds2_1
        this.dkScore = this.ds2_2
        // if (this.isFirstHt) {
        //   this.htAjax()
        //   this.isFirstHt = false
        // } else {
        //   this.dkRules = this.htRules
        // }
      } else {
        this.createRoomData3.substitute = true
        this.dkRound = this.ds3_1
        this.dkScore = this.ds3_2
        // if (this.isFirstDj) {
        //   this.djAjax()
        //   this.isFirstDj = false
        // } else {
        //   this.dkRules = this.djRules
        // }
      }
      setTimeout(() => {
        this.create()
      }, 1000)
    },
    create () {
      let type = null
      // 获取选择的游戏类型
      if (this.selectTypes === 0) {
        this.selectData = this.createRoomData1
        type = 1
        // this.showTip = true
        // this.tipMsg = '建设中...'
        // setTimeout(() => {
        //   this.showTip = false
        // }, 1000)
        // return
       // this.router = '/qt'
      } else if (this.selectTypes === 1) {
        this.selectData = this.createRoomData2
        type = 2
        this.showTip = true
        this.tipMsg = '建设中...'
        setTimeout(() => {
          this.showTip = false
        }, 1000)
        return
      // this.router = '/ht'
      } else {
        this.selectData = this.createRoomData3
        type = 3
      //  this.router = '/dj'
      }
      this.loadRoom = true
      let params = {
        userId: this.userInfo.id,
        baseScore: this.selectData.score,
        baseRound: this.selectData.round,
        substitute: this.selectData.substitute,
        gameType: type
      }
      console.log(params)
      let ajaxParams = window.JSON.stringify({
        host: this.$url,
        path: this.$interface['/room/create'],
        params: this.$sign(params)
      })
      // 调用android原生内部方法
      let vm = this
      this.$JsBridge.callHandler(
        'createRoom' // 原生的方法名
        , {'param': ajaxParams} // 带个原生方法的参数
        , function (responseData) { // 响应原生回调方法
          let data = window.JSON.parse(responseData)
          if (Number(data.code) === 200) {
            // vm.userId = data.model.createUserId
            // vm.roomId = data.model.id
            vm.isMaster = true
            vm.roomId = data.model.id
            vm.numId = data.model.numId
          //  if (vm.userInfo.roomNum) {
              // vm.userId = data.model.createUserId
              // vm.roomId = data.model.id
              // 创建房间房主才有邀请好友，保存房间号用于微信好友邀请
             // vm.numId = data.model.numId
            vm.$store.dispatch('saveCreateMsg', {
              roomId: data.model.id,
              numId: data.model.numId,
              baseScore: data.model.baseScore,
              baseRound: data.model.baseRound
            })
            // 进入房间
            vm.startEnterRoom(vm.numId)
            // } else {
            //   // 提示房卡不足
            //   vm.showTip = true
            //   vm.tipMsg = '房卡不足'
            //   setTimeout(() => {
            //     vm.showTip = false
            //   }, 1000)
            // }
            // userInfo.roomNum房卡数
            // vm.$store.dispatch('saveId', {
            //   userId: vm.userId,
            //   roomId: vm.roomId
            // })// vm.$store.dispatch('saveId', {
            //   userId: vm.userId,
            //   roomId: vm.roomId
            // })
           // vm.$router.push({path: vm.router, params: {userId: vm.userId, roomId: vm.roomId}})
          } else {
            vm.loadRoom = false
            vm.showTip = true
            vm.tipMsg = data.message
            setTimeout(() => {
              vm.showTip = false
            }, 1000)
          }
        }
      )
    },
    // 关闭创建房间弹窗
    closeCreateRoom () {
      this.showCreateRoom = false
    },
    // 加入房间弹窗
    joinRoom (e) {
      this.$audio.play(this.$audio.btn)
      this.showJoinRoom = true
    },
    // 加入房间选择数字
    selectNum (item, index, e) {
      this.$audio.play(this.$audio.ui)
      if (typeof item === 'string') {
        this.handleSelect(index)
        return false
      }
      // 填入数字
      if (this.numIndex > MAX_ROOM_NUM - 1) {
        return false
      }
      this.$set(this.roomNums, this.numIndex, item)
      this.numIndex++
      // 进入房间
      if (this.numIndex === MAX_ROOM_NUM) {
        let str = ''
        this.roomNums.forEach((item) => {
          str += item
        })
        this.loadRoom = true
        this.selectData.substitute = false
        this.startEnterRoom(str)
      }
    },
    startEnterRoom (str) {
      let params = {
        userId: this.userInfo.id,
        roomNumId: str
      }
      let ajaxParams = window.JSON.stringify({
        host: this.$url,
        path: this.$interface['/user/enter/room'],
        params: this.$sign(params)
      })
      let vm = this
      this.$JsBridge.callHandler(
        'joinRoom' // 原生的方法名
        , {'param': ajaxParams} // 带个原生方法的参数
        , function (responseData) { // 响应原生回调方法
          console.log('加入房间成功')
          let data = window.JSON.parse(responseData)
          if (Number(data.code) === 200) {
            vm.gameDatas.numId = data.model.numId
            vm.gameDatas.baseScore = data.model.baseScore
            vm.gameDatas.baseRound = data.model.baseRound
            vm.gameDatas.currentRound = data.model.currentRound
            vm.gameDatas.gameType = data.model.gameType
            console.log(data.model.id)
            vm.roomId = data.model.id
            vm.gameType = data.model.gameType
            console.log(vm.roomId)
            // 是否是代开房间
            if (!vm.selectData.substitute) {
              vm.enterRoom()
            } else {
              vm.showCreateRoom = false
              vm.loadRoom = false
              vm.roomNums.forEach((item, index) => {
                vm.$set(vm.roomNums, index, '')
              })
              vm.numIndex = 0
              vm.showDkModal = true
            }
          } else {
            vm.loadRoom = false
            vm.showTip = true
            vm.tipMsg = data.message
            setTimeout(() => {
              vm.showTip = false
              vm.roomNums.forEach((item, index) => {
                vm.$set(vm.roomNums, index, '')
              })
              vm.numIndex = 0
            }, 1000)
          }
        }
      )
    },
    enterRoom () {
      console.log(this.roomId)
      let type = ''
      if (this.gameType === 1) {
        type = 'qt'
      } else if (this.gameType === 2) {
        type = 'ht'
      } else if (this.gameType === 3) {
        type = 'dj'
      }
      let sign = this.$sn({
        userId: this.userId,
        roomId: this.roomId
      })
      let ajaxParams = window.JSON.stringify({
        host: this.$url,
        path: this.$interface['/app'],
        type: type,
        sign: sign,
        params: {
          command: 1001,
          data: {'roomId': this.roomId, 'userId': this.userId}
        }
      })
      let vm = this
      this.$JsBridge.callHandler(
        'enterRoom' // 原生的方法名
        , {'param': ajaxParams} // 带个原生方法的参数
        , function (responseData) { // 响应原生回调方法
          console.log('加入房间socket成功')
          vm.showCreateRoom = false
          vm.showJoinRoom = false
          vm.loadRoom = false
          vm.roomNums.forEach((item, index) => {
            vm.$set(vm.roomNums, index, '')
          })
          vm.numIndex = 0
          let data = window.JSON.parse(responseData)
          vm.$store.dispatch('saveUsers', data)
          // this.showDj = true
          if (vm.gameType === 1) {
            // vm.$router.push({path: '/qt', params: {}})
            vm.showQt = true
          } else if (vm.gameType === 2) {
            // vm.$router.push({path: '/ht', params: {}})
            vm.showHt = true
          } else if (vm.gameType === 3) {
            // vm.$router.push({path: '/dj', params: {}})
            vm.showDj = true
          }
        }
      )
    },
    // 加入房间弹窗数字处理(包括重输和删除功能)
    handleSelect (index) {
      this.$audio.play(this.$audio.ui)
      if (index === 0) {
        // 重输
        this.roomNums.forEach((item, index) => {
          this.$set(this.roomNums, index, '')
        })
        this.numIndex = 0
      } else if (index === 2) {
        // 删除
        if (this.numIndex < 0) {
          return false
        }
        this.numIndex--
        this.$set(this.roomNums, this.numIndex, '')
      }
    },
    // 选择牌面背景
    selectCardBg (url) {
      this.cardBg = url
    },
    // 选择桌面面背景
    selectDesktopBg (url) {
      this.desktopBg = url
    },
     // 关闭加入房间弹窗
    closeJoinRoom () {
      this.showJoinRoom = false
    },
    // 玩大九游戏
    closeDj (msg) {
      this.showDj = false
      this.showTip = true
      this.tipMsg = msg
      setTimeout(() => {
        this.showTip = false
      }, 1000)
    },
    exitDj () {
      this.showDj = false
    },
    // 玩清推游戏
    closeQt (msg) {
      this.showQt = false
      this.showTip = true
      this.tipMsg = msg
      setTimeout(() => {
        this.showTip = false
      }, 1000)
    },
    exitQt () {
      this.showQt = false
    },
    // 玩混推游戏
    closeHt (msg) {
      this.showHt = false
      this.showTip = true
      this.tipMsg = msg
      setTimeout(() => {
        this.showTip = false
      }, 1000)
    },
    exitHt () {
      this.showHt = false
    },
    closeDk () {
      this.showDkModal = false
    }
  }
}
</script>

